.digit-main {
  max-width: 360px;
  margin: auto;
  padding: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;

  text-align: center;
  height: 95vh;
  -ms-flex-item-align: center;
      align-self: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.digit-top {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-item-align: center;
      align-self: center;
}
.digit-top-text {
  -ms-flex-item-align: center;
      align-self: center;
  /* font-size: 26px; */
  font-size: 6rem;
  font-weight: bold;
  margin-left: 10px;
}

.digit-mid {
  margin-top: 40px;
  font-size: 1.5rem;
}

.digit-bottom-layout {
  margin-top: 40px;
  width: auto;
  height: 48px;
  border-radius: 300px;
  background-color: #000;
  -webkit-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
          box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);

  border: 5px;
  /* font-size: 14px; */
  font-size: 4rem;
  color: #ffffff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.digit-bottom-button {
  width: 30%;
  height: 40px;
  border-radius: 300px;
  background-color: #000000;
  -webkit-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
          box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
  cursor: pointer;
  font-size: 14px;

  color: #ffffff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.digit-bottom-text {
  text-align: center;
  margin-top: -15px;
}

@media screen and (min-width: 600px) {
  .digit-main {
    max-width: 400px;
  }
}



